<template>
    <div class="like">
        <!-- 头部icon -->
        <header>
          <van-nav-bar left-text="返回" title="我的收藏" left-arrow :border="false" @click-left="back">/></van-nav-bar>
        </header>
          <!-- 影片基本信息 -->
        <div class="likelist">
            <div class="likeitem clearfix" v-for="(item,index) in curLikeMovieData" :key="index" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
                <div class="like_img fl">
                   <img class="autoimg" :src="item.images.small" alt="">
                </div>
                <div class="like_msg fl">
                  <div class="mi-title one-txt">{{item.title}}</div>
                  <div>时长：{{item.durations[0]}}</div>
                  <div class="type_language">
                    <span>{{item.genresInfo}}</span>
                    <span>{{item.languages[0]}}</span>
                  </div>
                  <div class="mi_grade">橙子影视评分: {{item.rating.average}}分</div>
                </div>
                <div class="del" @click.stop="delLikeMovie(item.id)">
                    <van-icon name="cross" size="20" />
                </div>
            </div>
        </div>
    </div>
    <!-- 
        验证登录 _ 开启加载提示 _关闭加载提示 _获取所有用户收藏的电影数据 _ 获取当前用户收藏的电影数据
        _ 生成页面数据 _遍历数据_  back(){} _viewMovieDetail(o){} _ delLikeMovie(id){}

    -->
</template>

<script>
    import {createNamespacedHelpers} from 'vuex'
    const {mapState} = createNamespacedHelpers('likeMovieModule');
    export default {
        name:'likeMovie',

        computed:{
            ...mapState(['curLikeMovieData'])
        },

        methods:{
            // 返回上一步
            back() {
                this.$router.go(-1);
            },

            // 查看详情
            viewMovieDetail(o){
                this.$router.push(o);
            },

            // 删除收藏电影
            delLikeMovie(id){
                // console.log('id ==> ', id);
                for (let i = 0; i < this.curLikeMovieData.length; i++) {
                    if (id == this.curLikeMovieData[i].id) {
                        this.$store.dispatch('likeMovieModule/delLikeMovie', i)
                        .then(() => {
                            let userStatus = JSON.parse(localStorage.getItem("user"));

                             //获取所有用户电影数据
                            let likeMovieData = JSON.parse(localStorage.getItem('likeMovieData'));
                            likeMovieData[userStatus.username] = this.curLikeMovieData;
                            localStorage.setItem('likeMovieData', JSON.stringify(likeMovieData));
                        })
                        break;
                    }
                }
            }

        },

        created(){
            //验证登录
            let userStatus = localStorage.getItem("user");
            if (userStatus) {
                let userInfo = JSON.parse(userStatus);
                if (userInfo.isLogin) {

                //开启加载提示
                this.$toast.loading({
                //   duration: 0,
                  message: '加载中...'
                });

                // 获取所有用户收藏的电影数据
                let likeMovieData = localStorage.getItem('likeMovieData');
                likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

                //获取当前用户收藏的电影数据
                let curLikeMovieData = likeMovieData[userInfo.username];
                curLikeMovieData = curLikeMovieData ? curLikeMovieData : [];

                //生成页面数据
                this.$store.commit('likeMovieModule/getCurLikeMovieData', curLikeMovieData);

                this.$toast.clear();

                } else {
                    this.$router.push({name: 'login'}); //未登录
                }
            } else {
                this.$router.push({name: 'login'});//未登录
            }
            
        }
    }
</script>

<style lang="less" scoped>
.like{
    header{
        width: 100%;
        background-color: #697A9C;
        /deep/.van-nav-bar{
          background-color: transparent;
        }
        .van-nav-bar__text{
          color: #fff;
          font-size: 16px;
        }
        .van-nav-bar__title{
          color: #fff;
          font-size: 18px;
        }
        .van-icon-arrow-left:before{
           color: #fff;
           font-size: 20px;
        }
        /deep/.van-icon{
          padding: 0 6px;
      }
    }

    .likelist{
        position: fixed;
        top:47px;
        bottom:0;
        left: 0;
        right: 0;
        padding: 0 4px;
        overflow-y: auto;
        .likeitem{
            padding:6px 10px;
            background-image: linear-gradient(to top, #fff,rgba(255, 255, 255, 0.7),rgba(255, 255, 255, 0.9));
            box-shadow: 1px 1px 2px 3px #ccc;
            border-radius: 6px;
            position: relative;
            margin-bottom: 14px;
            .like_img{
                width: 100px;
                // height: 120px;
                overflow: hidden;
                background-color:#f5f5f5;
                >img{
                overflow: hidden;
                border-radius: 5px;
                }
            }
            .like_msg{
                width: calc(~"100% - 115px");
                margin-left: 15px;
                font-size: 14px;
                margin-top: 14px;
                >div{
                  line-height: 30px;
                }
                .mi-title{
                  // width: calc(~"100% - 120px");
                  font-size: 20px;
                  color: #000;
                  font-weight: 600;
                }
                .type_language{
                    span{
                        margin-right: 20px;
                    }
                }
                .mi_grade{
                  font-size: 16px;
                  font-weight: 700;
                  color: rgb(255, 1, 141);
                }
            }
            .del{
                position: absolute;
                top: 14px;
                right: 16px;
            }
        }
    }
}
</style>